import {
	mapState,
	mapMutations
} from 'vuex';
import uParse from '@/components/uParse/src/wxParse.vue'
import uniNoticeBar from "@/components/uni-notice-bar/uni-notice-bar.vue";
import uniPagination from "@/components/uni-pagination/uni-pagination.vue";
export default {
	components: {
		uniNoticeBar,
		uniPagination,
		uParse
	},
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			imageProp: {
				mode: 'aspectFit',
				padding: 0,
				lazyLoad: true,
				domain: "liangxuan.natapp1.cc"
			},
			// 公告弹窗
			modalName: 'Image',
			notice_pop_all: [],
			notice_current: 0,
			pop_notice_length: null,
			goodListAll: [],
			scroll_announcement: [],
			homeBrandList: [],
			hotGoodList: [],
			trueImgList: []
		};
	},
	computed: {
		...mapState(['token'])
	},
	onPullDownRefresh() {
		var _self = this;
		this.getTrueImgList();
		this.getHomeBrandList();
		this.getHotGoodList();
		this.loadData();
		setTimeout(() => {
			uni.stopPullDownRefresh();
		}, 600)
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...'
		})
		setTimeout(() => {
			this.getPopNotice();
			this.getTrueImgList();
			this.getHomeBrandList();
			this.getHotGoodList();
			this.loadData();
			uni.hideLoading();
		}, 600)
	},
	methods: {
		// 跳转到关于我们
		navToAboutUs(is_jump){
			if(is_jump){
				uni.navigateTo({
					url:'/pages/set/aboutUs/aboutUs'
				})
			}
		},
		
		getTrueImgList() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/picture/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode)
					this.trueImgList = res.data;
					this.getGoodListAll();
				}
			});
		},
		getHotGoodList() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/hot_good_class/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode)
					this.hotGoodList = res.data
				}
			});
		},
		getGoodListAll() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/home_good_type/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode)
					this.goodListAll = res.data
					for (var i = 0; i < this.goodListAll.length; i++) {
						this.goodListAll[i].bg_picture = this.trueImgList[i].picture
					}
				}
			});
		},
		getHomeBrandList() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/home_brand/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode)
					this.homeBrandList = res.data

				}
			});
		},
		// 解决蒙层滚动问题
		moveHandle() {
			return;
		},
		// 测试数据
		navToList(detail_url, title_name) {
			if (detail_url == 'more') {
				uni.showLoading({
					title: "跳转中..."
				})
				setTimeout(() => {
					uni.switchTab({
						url: '../category/category'
					})
					uni.hideLoading();
				}, 500)
			} else {
				var data = {
					detail_url: detail_url,
					title_name: title_name
				}
				uni.navigateTo({
					url: '/pages/product/list?data=' + JSON.stringify(data)

				})
			}
		},
		hideModal(e) {
			this.modalName = null
		},
		pagination_change(e) {
			this.notice_current = e.current - 1;
		},
		getScroll_announcements() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/announcements/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode);
					// this.scroll_announcement=res.data.results
					for (var i = 0; i < res.data.results.length; i++) {
						var announcementItem = "【" + res.data.results[i].title + "】" + "　" + res.data.results[i].content + "　　　　　　";
						this.scroll_announcement.push(announcementItem);
					}
					this.scroll_announcement.join("|==|");
				}
			});
		},
		getBanners() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/banners/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					res.data.results
					let carouselList = res.data;
					this.swiperLength = carouselList.length;
					this.carouselList = carouselList;
				}
			});
		},
		getPopNotice() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/popnotice/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					// res.data = res.data.content.replace(/\<img/gi, '<img style="max-width:100%;height:auto" ')
					this.pop_notice_length = res.data.length;
					this.notice_pop_all = res.data;
				}
			});
		},
		loadData() {
			// 加载banners
			this.getBanners();
			this.getScroll_announcements();
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
		},
		//详情页
		navToDetailPage(item, type) {
			var item_url = item.url
			var title_name = item.brand_name
			if (type == 'class') {
				var data = {
					detail_url: item_url,
					title_name: title_name
				}
				uni.navigateTo({
					url: '/pages/product/list?data=' + JSON.stringify(data)

				})
			} else {
				uni.navigateTo({
					url: `/pages/product/product?item_url=${item_url}`
				})
			}
			return;
		},
	},
}
